body {
  margin: 0;
  padding: 0;

	
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);

	height: 40em;
	width: 40em;

	display: -webkit-flex;

	display: flex;
	-webkit-flex-flow: row nowrap;
	        flex-flow: row nowrap;
	-webkit-align-content: stretch;
	        align-content: stretch;
	-webkit-justify-content: space-between;
	        justify-content: space-between;

	border-radius: 0.5em;
	background-color: #f2f2f2;
	overflow: hidden;

	box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.5);
}
.close {
	position: relative;
	
	text-decoration: none;
	font-size: 1.5em;
	font-weight: 900;

	color: #000;
	border: none;

	cursor: pointer;
	background-color: inherit;
}
.close:hover {
	top: -1px;
	-webkit-filter: drop-shadow(3px 3px 7px rgba(0, 0, 0, 0.9));
	        filter: drop-shadow(3px 3px 7px rgba(0, 0, 0, 0.9));
}
.close:active {
	top: 1px;
}

.item{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: row nowrap;
	        flex-flow: row nowrap;
	-webkit-align-items: baseline;
	        align-items: baseline;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	
	-webkit-transition: all 1s ease-out;
	
	transition: all 1s ease-out;
}

.item-link {
	width: 80%;
	text-decoration: none;
	font-size: 1em;
	font-weight: 900;
	cursor: pointer;
}
.item-link:hover {
	text-decoration: underline;
}
.custom-button {
	border: none;
	border-radius: .3em;
	background: rgb(170, 170, 170, 0.5);
	padding: 0.4em 0.8em;
	margin: 0 1px 0 1px;
	font-size: 1em;
	font-weight: 900;
	cursor: pointer;
	display: inline-block;
	-webkit-transition: all 0.3s ease-out;
	transition: all 0.3s ease-out;
}
/* On mouse-over */
.custom-button:hover {
	color: #fff;
	background: rgb(170, 170, 170);
	box-shadow: 1px 1px 3px 3px #797979;
}
/* On mouse-click */
.custom-button:active {
	box-shadow: inset 0 0 10px #797979;
}

.Nav-panel{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: column nowrap;
	        flex-flow: column nowrap;
	border-right: .1em solid #696969;
	padding: 1em;
	width: 30%;
}
button {
	margin-bottom: .5em;
}
.list-items {
    margin: 1em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column; 
}
.checkbox-custom {
    -webkit-transform: scale(2);
            transform: scale(2);
    margin: 1em 2em;
}
.checkbox-label {
    font-size: 1.4em;
}
.text-input {
	/* margin-right: 1em; */
	vertical-align: middle;
	font-size: 1em;
	padding: 10px;
	background-color: #fff;
	border: 1px solid #ddd;
	border-radius: .3em;
}
.list {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	width: 70%;
	padding: 0 1em;
}
.flex-nowrap {
	display: -webkit-flex;
	display: flex;
	-webkit-flex: nowrap 1;
	        flex: nowrap 1;
}
#list-title {
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}
#add-list-input {
	display: grid;
	grid-template-columns: 80% 20%;
}


.popup {
	position: absolute;
	z-index: 1;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	margin: auto;
	background-color: rgba(0, 0, 0, 0.5);
}
.popup-inner {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: column nowrap;
	        flex-flow: column nowrap;

	padding: 1em;
	background: #f2f2f2;
	width: 45%;
	border-radius: 0.5em;
	
	box-shadow: .5em .5em 1em 1em #4d4e53;
}
.popup-title {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: row nowrap;
	        flex-flow: row nowrap;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}
.button-form {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
	margin-top: 1em;
}
.input {
	margin-right: 1em;
	vertical-align: middle;
	font-size: 1em;
	padding: 10px;
	background-color: #fff;
	border: 1px solid #ddd;
	border-radius: .3em;
}

